<template>
  <v-container>
    <!-- Contact Form -->
    <v-form @submit.prevent="addContact">
      <v-row>
        <v-col cols="12" md="4">
          <v-text-field
            v-model="newContact.name"
            label="Name"
            required
            color="deep-purple accent-4"
            outlined
          ></v-text-field>
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field
            v-model="newContact.phone"
            label="Phone"
            required
            color="deep-purple accent-4"
            outlined
          ></v-text-field>
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field
            v-model="newContact.email"
            label="Email"
            color="deep-purple accent-4"
            outlined
          ></v-text-field>
        </v-col>
        <v-col cols="12">
          <v-btn type="submit" color="deep-purple accent-4" class="mt-4" large>
            Add Contact
          </v-btn>
        </v-col>
      </v-row>
    </v-form>

    <!-- Contacts Grid -->
    <v-row>
      <v-col v-for="contact in contacts" :key="contact.id" cols="12" md="4" lg="3">
        <v-card class="mx-auto my-4" max-width="344" elevation="2">
          <v-card-title>
            <span class="headline">{{ contact.name }}</span>
          </v-card-title>
          <v-card-subtitle>
            {{ contact.phone }}
          </v-card-subtitle>
          <v-card-subtitle>
            {{ contact.email }}
          </v-card-subtitle>
          <v-card-actions>
            <v-btn icon @click="editContact(contact)" color="blue">
              <v-icon>mdi-pencil</v-icon>
            </v-btn>
            <v-btn icon @click="deleteContact(contact.id)" color="red">
              <v-icon>mdi-delete</v-icon>
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>

    <!-- Edit Dialog -->
    <v-dialog v-model="editDialog" max-width="500px">
      <v-card>
        <v-card-title>Edit Contact</v-card-title>
        <v-card-subtitle>
          <v-form @submit.prevent="updateContact">
            <v-text-field
              v-model="selectedContact.name"
              label="Name"
              required
              color="deep-purple accent-4"
              outlined
            ></v-text-field>
            <v-text-field
              v-model="selectedContact.phone"
              label="Phone"
              required
              color="deep-purple accent-4"
              outlined
            ></v-text-field>
            <v-text-field
              v-model="selectedContact.email"
              label="Email"
              color="deep-purple accent-4"
              outlined
            ></v-text-field>
            <v-card-actions>
              <v-btn color="deep-purple accent-4" @click="updateContact">Update</v-btn>
              <v-btn text @click="editDialog = false">Cancel</v-btn>
            </v-card-actions>
          </v-form>
        </v-card-subtitle>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      contacts: [],
      newContact: {
        name: '',
        phone: '',
        email: ''
      },
      editDialog: false,
      selectedContact: {}
    };
  },
  created() {
    this.fetchContacts();
  },
  methods: {
    async fetchContacts() {
      try {
        const response = await axios.get('http://localhost:3000/contacts');
        this.contacts = response.data;
      } catch (error) {
        console.error('Error fetching contacts:', error);
      }
    },
    async addContact() {
      if (!this.newContact.name || !this.newContact.phone) {
        alert('Name and phone are required.');
        return;
      }

      try {
        const response = await axios.post('http://localhost:3000/contacts', this.newContact);
        this.contacts.push(response.data);
        this.newContact = { name: '', phone: '', email: '' };
      } catch (error) {
        console.error('Error adding contact:', error);
      }
    },
    async deleteContact(id) {
      try {
        await axios.delete(`http://localhost:3000/contacts/${id}`);
        this.contacts = this.contacts.filter(contact => contact.id !== id);
      } catch (error) {
        console.error('Error deleting contact:', error);
      }
    },
    editContact(contact) {
      console.log('Editing contact:', contact); // Debugging line
      this.selectedContact = { ...contact };
      this.editDialog = true;
    },
    async updateContact() {
      console.log('Attempting to update contact:', this.selectedContact); // Debugging line
      try {
        await axios.put(`http://localhost:3000/contacts/${this.selectedContact.id}`, this.selectedContact);
        const index = this.contacts.findIndex(contact => contact.id === this.selectedContact.id);
        if (index !== -1) {
          this.contacts.splice(index, 1, this.selectedContact);
        }
        this.editDialog = false;
      } catch (error) {
        console.error('Error updating contact:', error);
      }
    }
  }
};
</script>

<style scoped>
.v-container {
  margin-top: 20px;
}

.v-card {
  transition: transform 0.2s;
}

.v-card:hover {
  transform: scale(1.05);
}

.v-btn {
  font-weight: bold;
}

.v-text-field {
  margin-bottom: 20px;
}
</style>
