import Vue from 'vue';
import VueRouter from 'vue-router';
import HomePageComponent from '../views/HomePageComponent.vue'; // or HomePageView

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'HomePageComponent', // or 'HomePageView'
    component: HomePageComponent // or HomePageView
  }
];

const router = new VueRouter({
  routes
});

export default router;
